<template>
    <div :class="classes" :style="style">
        <div :class="prefixCls + '-header'">
            <Checkbox :checked="checkedAll" value= "" :disabled="checkedAllDisabled" @on-change="toggleSelectAll"></Checkbox>
            <span>{{ title }}</span>
            <span :class="prefixCls + '-header-count'">{{ count }}</span>
        </div>
        <div :class="bodyClasses">
            <div :class="prefixCls + '-body-search-wrapper'" v-if="filterable">
                <Search
                    :prefix-cls="prefixCls + '-search'"
                    :query="query"
                    :placeholder="filterPlaceholder"></Search>
            </div>
            <ul :class="prefixCls + '-content'">
                <li
                    v-for="(item, index) in showItems"
                    :class="itemClasses(item)"
                    @click.prevent="select(item)">
                    <Checkbox :checked="isCheck(item)" value= "" :disabled="item.disabled"></Checkbox>
                    <span>{{ showLabel(item) }}</span>
                </li>
                <li :class="prefixCls + '-content-not-found'">{{ notFoundText }}</li>
            </ul>
        </div>
        <div :class="prefixCls + '-footer'" v-if="showFooter" ref="footer"><slot></slot></div>
    </div>
</template>

<script type="text/javascript">
    import Search from './search.vue';
    import Checkbox from '../checkbox/checkbox.vue';

    export default {
        components: { Search, Checkbox },
        props: {
            prefixCls: String,
            data: Array,
            renderFormat: Function,
            checkedKeys: Array,
            style: Object,
            title: [String, Number],
            filterable: Boolean,
            filterPlaceholder: String,
            filterMethod: Function,
            notFoundText: String,
            validKeysCount: Number
        },
        data () {
            return {
                showItems: [],
                query: '',
                showFooter: true,
                d_checkedKeys:this.checkedKeys,
                d_data:this.data
            };
        },
        computed: {
            classes () {
                return [
                    `${this.prefixCls}`,
                    {
                        [`${this.prefixCls}-with-footer`]: this.showFooter
                    }
                ];
            },
            bodyClasses () {
                return [
                    `${this.prefixCls}-body`,
                    {
                        [`${this.prefixCls}-body-with-search`]: this.filterable,
                        [`${this.prefixCls}-body-with-footer`]: this.showFooter
                    }
                ];
            },
            count () {
                const validKeysCount = this.validKeysCount;
                return (validKeysCount > 0 ? `${validKeysCount}/` : '') + `${this.d_data.length}`;
            },
            checkedAll () {
                return this.d_data.filter(data => !data.disabled).length === this.validKeysCount && this.validKeysCount !== 0;
            },
            checkedAllDisabled () {
                return this.d_data.filter(data => !data.disabled).length <= 0;
            }
        },
        methods: {
            itemClasses (item) {
                return [
                    `${this.prefixCls}-content-item`,
                    {
                        [`${this.prefixCls}-content-item-disabled`]: item.disabled
                    }
                ];
            },
            showLabel (item) {
                return this.renderFormat(item);
            },
            isCheck (item) {
                return this.d_checkedKeys.some(key => key === item.key);
            },
            select (item) {
                if (item.disabled) return;
                const index = this.d_checkedKeys.indexOf(item.key);
                index > -1 ? this.d_checkedKeys.splice(index, 1) : this.d_checkedKeys.push(item.key);
            },
            updateFilteredData () {
                this.showItems = this.d_data;
            },
            toggleSelectAll (status) {
                this.d_checkedKeys = status ?
                        this.d_data.filter(data => !data.disabled || this.d_checkedKeys.indexOf(data.key) > -1).map(data => data.key) :
                        this.d_data.filter(data => data.disabled && this.d_checkedKeys.indexOf(data.key) > -1).map(data => data.key);
            },
            filterData (value) {
                return this.filterMethod(value, this.query);
            }
        },
        created () {
            this.updateFilteredData();

        },
        mounted () {
            this.showFooter = this.$refs.footer.innerHTML !== '';
        },
        watch: {
            data (val) {
                this.d_data=val;
                this.updateFilteredData();
            },
            d_checkedKeys(val){
                this.$emit('on-checked-change',val);
            },
            checkedKeys(val){
                this.d_checkedKeys=val;
            }
        }
    };
</script>



