<script type="text/javascript">
    import querystring from 'querystring';

    let queryMap = querystring.parse(location.search.substr(1));

    export default {
        data() {
            return {}
        },
        render: function (createElement) {
            return createElement('a', {
                attrs: {
                    href: `#${this.to}`
                }
            }, this.$slots.default)
        },
        props: {
            to: {
                type: String,
                required: true
            }
        }
    }
</script>



