<template>
    <div :class="classes">
        <slot></slot>
    </div>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    const prefixCls = `${prefix}breadcrumb`;

    export default {
        name: 'Breadcrumb',
        props: {
            separator: {
                type: String,
                default: '>'
            }
        },
        computed: {
            classes () {
                return `${prefixCls}`;
            }
        },
        mounted () {
            this.updateChildren();
        },
        methods: {
            updateChildren () {
                this.$children.forEach((child) => {
                    // 通过子元素的data传值
                    child.separator = this.separator;
                });
            }
        },
        watch: {
            separator () {
                this.updateChildren();
            }
        }
    };
</script>


