<template>
    <i :class="classes" :style="styles" @click="clickHandler" :title="title"></i>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    const prefixCls = `${prefix}icon`;

    export default {
        name: 'Icon',
        props: {
            type: String,
            size: [Number, String],
            color: String,
            title: String
        },
        methods: {
            clickHandler(event) {
                this.$emit('click', event);
            }
        },
        computed: {
            classes () {
                return `${prefixCls} ${prefixCls}-${this.type}`;
            },
            styles () {
                let style = {};
                if (this.title && this.title !=='') {
                    style['font-size'] = `32px`;

                }
                if (this.size) {
                    style['font-size'] = `${this.size}px`;
                }

                if (this.color) {
                    style.color = this.color;
                }

                return style;
            }
        }
    };
</script>


