<template>
    <span>
        <ul v-if="data && data.length" :class="[prefixCls + '-menu']">
            <Casitem
                    v-for="item in data"
                    :prefix-cls="prefixCls"
                    :data.sync="item"
                    :tmp-item="tmpItem"
                    @click.stop="handleClickItem(item)"
                    @mouseenter.stop="handleHoverItem(item)"></Casitem>
        </ul>
        <Caspanel v-if="d_sublist && d_sublist.length" :prefix-cls="prefixCls" :data.sync="d_sublist" :disabled="disabled" :trigger="trigger" :change-on-select="changeOnSelect">

        </Caspanel>
    </span>
</template>
<script>
    import Casitem from './casitem.vue';
    import emitter from '../../utils/emitter';
    export default {
        name:'Caspanel',
        componentName:"Caspanel",
        mixins: [emitter],
        components: { Casitem },
        props: {
            data: {
                type: Array,
                default () {
                    return [];
                }
            },
            sublist: {
                type: Array,
                default () {
                    return [];
                }
            },
            disabled: Boolean,
            changeOnSelect: Boolean,
            trigger: String,
            prefixCls: String
        },
        data () {
            return {
                tmpItem: {},
                result: [],
                d_sublist:this.sublist
            };
        },
        methods: {
            handleClickItem (item) {
                if (this.trigger !== 'click' && item.children) return;
                this.handleTriggerItem(item);
            },
            handleHoverItem (item) {
                if (this.trigger !== 'hover' || !item.children) return;
                this.handleTriggerItem(item);
            },
            handleTriggerItem (item, fromInit = false) {
                if (item.disabled) return;

                // return value back recursion
                const backItem = this.getBaseItem(item);
                this.tmpItem = backItem;
                this.emitUpdate([backItem]);
debugger
                if (item.children && item.children.length){

                    this.d_sublist = item.children;
                    this.dispatch('Cascader', 'on-result-change', [false, this.changeOnSelect, fromInit]);
                } else {
                    this.d_sublist = [];
                    this.dispatch('Cascader', 'on-result-change', [true, this.changeOnSelect, fromInit]);
                }
            },
            updateResult (item) {
                this.result = [this.tmpItem].concat(item);
                this.emitUpdate(this.result);
            },
            getBaseItem (item) {
                let backItem = Object.assign({}, item);
                if (backItem.children) {
                    delete backItem.children;
                }

                return backItem;
            },
            emitUpdate (result) {
                this.dispatch('Caspanel', 'on-emit-update', [result]);
                console.log(this.$parent.$parent.$parent.$options.name);
                debugger
                //if (this.$parent.$options.name === 'Caspanel') {
                   // this.$parent.updateResult(result);
                //} else {
                  //  this.$parent.$parent.updateResult(result);
                //}
            }
        },
        watch: {
            data () {
                this.d_sublist = [];
            },
            sublist(val){
                this.d_sublist=val;
            }
        },
        mounted() {
            this.$on('on-emit-update',(result)=>{
                this.updateResult(result);
            });

            this.$on('on-find-selected',(val)=>{
                let value = [...val];
                for (let i = 0; i < value.length; i++) {
                    for (let j = 0; j < this.data.length; j++) {
                        if (value[i] === this.data[j].value) {
                        debugger
                            this.handleTriggerItem(this.data[j], true);
                            value.splice(0, 1);
                            this.$nextTick(() => {
                                this.broadcast('Caspanel','on-find-selected', value);
                            });
                            return false;
                        }
                    }
                }
            });

             this.$on('on-clear',()=>{
                this.d_sublist = [];
                this.tmpItem = {};
            });


        }
    };
</script>



