<template>
    <ul :class="classes">
        <slot></slot>
    </ul>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    const prefixCls = `${prefix}timeline`;

    export default {
        name: 'Timeline',
        props: {
            pending: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-pending`]: this.pending
                    }
                ];
            }
        }
    };
</script>


