<template>
    <label :class="wrapClasses">
        <span :class="checkboxClasses">
            <span :class="innerClasses"></span>
            <input
                v-if="group"
                type="checkbox"
                :class="inputClasses"
                :disabled="disabled"
                :value="value"
                v-model="model"
                @change="change">
            <input
                v-if="!group"
                type="checkbox"
                :class="inputClasses"
                :disabled="disabled"
                v-model="selected"
                @change="change">
        </span>
        <slot v-if="showSlot"><span ref="slot">{{ value }}</span></slot>
    </label>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    import emitter from '../../utils/emitter';
    const prefixCls = `${prefix}checkbox`;

    export default {
        mixins: [emitter],
        props: {
            disabled: {
                type: Boolean,
                default: false
            },
            value: {
                type: [String, Number, Boolean],
                default:''
            },
            checked: {
                type: Boolean,
                default: false
            }
        },
        data () {
            return {
                model: [],
                selected: this.checked,
                group: false,
                showSlot: true
            };
        },
        computed: {
            wrapClasses () {
                return [
                    `${prefixCls}-wrapper`,
                    {
                        [`${prefixCls}-group-item`]: this.group,
                        [`${prefixCls}-wrapper-checked`]: this.selected,
                        [`${prefixCls}-wrapper-disabled`]: this.disabled
                    }
                ];
            },
            checkboxClasses () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-checked`]: this.selected,
                        [`${prefixCls}-disabled`]: this.disabled
                    }
                ];
            },
            innerClasses () {
                return `${prefixCls}-inner`;
            },
            inputClasses () {
                return `${prefixCls}-input`;
            }
        },
        mounted () {
            this.$nextTick(function () {
	            if (this.$parent && this.$parent.$options.name === 'checkboxGroup') this.group = true;
                if (!this.group) {
                    this.updateModel();
                    if (this.$refs.slot && this.$refs.slot.innerHTML === '') {
                       this.showSlot = false;
                    }
                }
            });
        },
        methods: {
            change (event) {
                if (this.disabled) {
                    return false;
                }
                this.selected = event.target.checked;
                if (this.group) {
                    this.$parent.change(this.model);
                } else {
                    this.$emit('on-change', this.selected);
                    this.dispatch('checkboxGroup', 'on-form-change', [this.selected]);
                    // this.$emit('on-form-change', this.selected);
                }
            },
            updateModel () {
                this.selected = this.checked;
            }
        },
        watch: {
            checked () {
               this.updateModel();
            },
            /**
             * 功能: table中全选出现死循环 暂时注释 造成影响：checkbox外部控制绑定值改变后没有on-change事件抛出
             * 修改记录: xx
             * @author 叶灵晓
             * @date 2017-01-18 19:31
             */

            // selected(val){
            //     if (this.group) {
            //         this.$parent.change(this.model);
            //     } else {
            //         this.$emit('on-change', val);
            //         this.$emit('on-form-change', val);
            //     }
            // }
        }
    };
</script>


