<template>
    <div :class="wrapClasses"
         @mouseenter="handleInputMouseenter"
         @mouseleave="handleInputMouseleave">
        <template v-if="type !== 'textarea'">
            <div :class="[prefixCls + '-group-prepend']" v-if="$slots.prepend"><slot name="prepend"></slot></div>
            <i :class="[prefix+'icon', prefix+'icon-'+icon, prefixCls+'-icon']" v-if="icon" @click="handleIconClick"></i>
            <input  :type="type"
                    :name="name"
                    :class="inputClasses"
                    :placeholder="placeholder"
                    :disabled="disabled"
                    :maxlength="maxlength"
                    :readonly="readonly"
                    :value="currentValue"
                    @focus="handleFocus"
                    @blur="handleBlur"
                    @input="handleInput"
            />
            <div :class="[prefixCls + '-group-append']" v-if="$slots.append" ><slot name="append"></slot></div>
        </template>
        <template v-if="type === 'textarea'">
        <textarea   ref="textarea"
                    :class="textareaClasses"
                    :style="textareaStyles"
                    :placeholder="placeholder"
                    :disabled="disabled"
                    :rows="rows"
                    :maxlength="maxlength"
                    :readonly="readonly"
                    :name="name"
                    :value="currentValue"
                    @focus="handleFocus"
                    @blur="handleBlur"
                    @input="handleInput" >
        </textarea>
        </template>
    </div>

</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    import { oneOf } from '../../utils/assist';
    import calcTextareaHeight from '../../utils/calcTextareaHeight';
    import emitter from '../../utils/emitter';
    const prefixCls = `${prefix}input`;
    export default {
        mixins: [emitter],
        props: {
            type: {
                validator (value) {
                    return oneOf(value, ['text', 'textarea', 'password']);
                },
                default: 'text'
            },
            value: {
                type: [String, Number],
                default: ''
            },
            size: {
                validator (value) {
                    return oneOf(value, ['small', 'large']);
                }
            },
            placeholder: {
                type: String,
                default: ''
            },
            maxlength: {
                type: Number
            },
            disabled: {
                type: Boolean,
                default: false
            },
            icon: String,
            autosize: {
                type: [Boolean, Object],
                default: false
            },
            rows: {
                type: Number,
                default: 2
            },
            readonly: {
                type: Boolean,
                default: false
            },
            name: {
                type: String
            },
            autofocus: {
                type: Boolean,
                default: false
            }
        },
        data () {
            return {
                prefix: prefix,
                prefixCls: prefixCls,
                textareaStyles: {},
                currentValue: this.value,
            };
        },
        methods: {
            handleInputMouseenter(){
                this.$emit('mouseenter');
            },
            handleInputMouseleave(){
                this.$emit('mouseleave');
            },
            handleInput (event) {
                this.setCurrentValue(event.target.value);
            },
            handleBlur(event) {
                this.$emit('blur', event);
                /**
                 * 功能: xx
                 * 修改记录: 冒泡至form-item 校验
                 * @author 叶灵晓
                 * @date 2017-01-22 15:19
                 */
                this.dispatch('form-item', 'on-form-blur', [this.currentValue]);
            },
            handleFocus(event) {
                this.$emit('focus', event);
            },
            handleIconClick(event) {
                this.$emit('click', event);
            },
            resizeTextarea () {
                const autosize = this.autosize;
                if (!autosize || this.type !== 'textarea') {
                    return false;
                }
                const minRows = autosize.minRows;
                const maxRows = autosize.maxRows;
                this.textareaStyles = calcTextareaHeight(this.$refs.textarea, minRows, maxRows);
            },
            setCurrentValue(value){
                this.currentValue = value;
                this.$nextTick(()=> {
                    this.resizeTextarea();
                });
                this.$emit('input', value);
                this.$emit('change', value);
                /**
                 * 功能: xx
                 * 修改记录: 不限层级关系，必须使用冒泡
                 * @author 叶灵晓
                 * @date 2017-01-22 15:12
                 */
                this.dispatch('form-item', 'on-form-change', [value]);
            }

        },
        computed: {
            wrapClasses () {
                return [
                    `${prefixCls}-wrapper`,
                    {
                        [`${prefixCls}-wrapper-${this.size}`]: !!this.size,
                        [`${prefixCls}-type`]: this.type,
                        [`${prefixCls}-group`]: this.$slots.prepend || this.$slots.append,
                        [`${prefixCls}-group-${this.size}`]: (this.$slots.prepend || this.$slots.prepend) && !!this.size
                    }
                ];
            },
            inputClasses () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-${this.size}`]: !!this.size,
                        [`${prefixCls}-disabled`]: this.disabled
                    }
                ];
            },
            textareaClasses () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-disabled`]: this.disabled
                    }
                ];
            }
        },
        watch: {
            value (val) {
                this.setCurrentValue(val);
            }
        },
        mounted () {
            this.$nextTick(()=> {
                if(this.autofocus){
                    this.$refs.input.focus();
                }
                this.resizeTextarea();
            });
        }
    };
</script>


