<template>
    <li :class="classes" @click="handleClick"><slot></slot></li>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    const prefixCls = `${prefix}dropdown-item`;

    export default {
        props: {
            itemKey: {
                type: [String, Number]
            },
            disabled: {
                type: Boolean,
                default: false
            },
            selected: {
                type: Boolean,
                default: false
            },
            divided: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-disabled`]: this.disabled,
                        [`${prefixCls}-selected`]: this.selected,
                        [`${prefixCls}-divided`]: this.divided
                    }
                ];
            }
        },
        methods: {
            handleClick () {
                const $parent = this.$parent.$parent.$parent;
                const hasChildren = this.$parent.$parent && this.$parent.$parent.$options.name === 'Dropdown';

                if (this.disabled) {
                    this.$nextTick(() => {
                        $parent.visible = true;
                    });
                } else if (hasChildren) {
                    this.$parent.$parent.$emit('on-haschild-click');
                } else {
                    if ($parent && $parent.$options.name === 'Dropdown') {
                        $parent.$emit('on-hover-click');
                    }
                }
                $parent.$emit('on-click', this.itemKey);
            }
        }
    };
</script>


