// es6 polyfill
import 'core-js/fn/array/find-index';

import { ALink, PanLink } from './components/link';
import Affix from './components/affix';
import Alert from './components/alert';
import BackTop from './components/back-top';
import Badge from './components/badge';
import Breadcrumb from './components/breadcrumb';
import Button from './components/button';
import Card from './components/card';
import Cascader from './components/cascader';
import Checkbox from './components/checkbox';
import AwCircle from './components/circle';
import Collapse from './components/collapse';
import DatePicker from './components/date-picker';
import Dropdown from './components/dropdown';
import Form from './components/form';
import Icon from './components/icon';
import Input from './components/input';
import InputNumber from './components/input-number';
import LoadingBar from './components/loading-bar';
import Menu from './components/menu';
import Message from './components/message';
import Modal from './components/modal';
import Notice from './components/notice';
import Page from './components/page';
import Poptip from './components/poptip';
import Progress from './components/progress';
import Radio from './components/radio';
import Slider from './components/slider';
import Spin from './components/spin';
import Steps from './components/steps';
import Switch from './components/switch';
import Table from './components/table';
import Tabs from './components/tabs';
import Tag from './components/tag';
import Timeline from './components/timeline';
import TimePicker from './components/time-picker';
import Tooltip from './components/tooltip';
import Transfer from './components/transfer';
import Upload from './components/upload';
import { Row, Col } from './components/layout';
import { Select, Option, OptionGroup } from './components/select';


const awifi = {
    ALink,
    PanLink,
    Affix,
    Alert,
    BackTop,
    Badge,
    Breadcrumb,
    BreadcrumbItem: Breadcrumb.Item,
    aButton: Button,
    aButtonGroup: Button.Group,
    Goback: Button.Goback,
    Card,
    aCascader:Cascader,
    aCheckbox:Checkbox,
    aCheckboxGroup: Checkbox.Group,
    AwCircle,
    aDatePicker:DatePicker,
    aDropdown:Dropdown,
    aDropdownItem: Dropdown.Item,
    aDropdownMenu: Dropdown.Menu,
    aForm: Form,
    aFormItem: Form.Item,
    aCol: Col,
    Collapse,
    aIcon:Icon,
    aInput: Input,
    InputNumber,
    LoadingBar,
    Menu,
    MenuGroup: Menu.Group,
    MenuItem: Menu.Item,
    Submenu: Menu.Sub,
    Message,
    aModal:Modal,
    aNotice:Notice,
    aOption: Option,
    aOptionGroup:OptionGroup,
    aPage:Page,
    Panel: Collapse.Panel,
    aPoptip:Poptip,
    aProgress:Progress,
    aRadio:Radio,
    aRadioGroup: Radio.Group,
    aRow:Row,
    aSelect: Select,
    aSlider:Slider,
    Spin,
    Step: Steps.Step,
    Steps,
    aSwitch:Switch,
    aTable: Table,
    aTabs: Tabs,
    aTabPane: Tabs.Pane,
    aTag:Tag,
    Timeline,
    TimelineItem: Timeline.Item,
    aTimePicker:TimePicker,
    aTooltip:Tooltip,
    aTransfer:Transfer,
    aUpload:Upload
};

const install = function (Vue) {
    Object.keys(awifi).forEach((key) => {
        Vue.component(key, awifi[key]);
    });

    Vue.prototype.$Loading = LoadingBar;
    Vue.prototype.$Message = Message;
    Vue.prototype.$Modal = Modal;
    Vue.prototype.$Notice = Notice;
};

// auto install
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

module.exports = Object.assign(awifi, {install});   // eslint-disable-line no-undef



