<template>
    <div
        :class="[prefixCls]"
        v-clickoutside="handleClose"
        @mouseenter="handleMouseenter"
        @mouseleave="handleMouseleave">
        <div :class="[prefixCls]" ref="reference" @click="handleClick"><slot></slot></div>
        <Drop v-show="d_visible" :placement="placement" :transition="transition" ref="drop"><slot name="list"></slot></Drop>
    </div>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    import Drop from '../select/dropdown.vue';
    import clickoutside from '../../directives/clickoutside';
    import { oneOf } from '../../utils/assist';

    const prefixCls = `${prefix}dropdown`;

    export default {
        name: 'Dropdown',
        directives: { clickoutside },
        components: { Drop },
        props: {
            trigger: {
                validator (value) {
                    return oneOf(value, ['click', 'hover','custom']);
                },
                default: 'hover'
            },
            placement: {
                validator (value) {
                    return oneOf(value, ['top', 'top-start', 'top-end', 'bottom', 'bottom-start', 'bottom-end', 'left', 'left-start', 'left-end', 'right', 'right-start', 'right-end']);
                },
                default: 'bottom'
            },
            //yelx ：由data更改到属性
            visible: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            transition () {
                return ['bottom-start', 'bottom', 'bottom-end'].indexOf(this.placement) > -1 ? 'slide-up' : 'fade';
            }
        },
        data () {
            return {
                prefixCls: prefixCls,
                d_visible:this.visible
            };
        },
        methods: {
            handleClick () {
                if (this.trigger === 'click') {
                    this.d_visible = !this.d_visible;
                }
            },
            handleMouseenter () {
                if (this.trigger === 'hover') {
                    clearTimeout(this.timeout);
                    this.timeout = setTimeout(() => {
                        this.d_visible = true;
                    }, 250);
                }
            },
            handleMouseleave () {
                if (this.trigger === 'hover') {
                    clearTimeout(this.timeout);
                    this.timeout = setTimeout(() => {
                        this.d_visible = false;
                    }, 150);
                }
            },
            handleClose () {
                if (this.trigger === 'click') {
                    this.d_visible = false;
                }
            },
            hasParent () {
                const $parent = this.$parent.$parent;
                if ($parent && $parent.$options.name === 'Dropdown') {
                    return $parent;
                } else {
                    return false;
                }
            }
        },
        watch: {
            visible (val) {
                this.d_visible=val;
                if (val) {
                    this.$refs.drop.update();
                } else {
                    this.$refs.drop.destroy();
                }
            },
            //yelx 监听副本变化
            d_visible (val) {
                this.$emit('on-visible-change', val);
            },
        },
        events: {
            'on-click' (key) {
                const $parent = this.hasParent();
                if ($parent ) $parent.$emit('on-click', key);
            },
            'on-hover-click' () {
                const $parent = this.hasParent();
                if ($parent) {
                    this.$nextTick(() => {
                        this.d_visible = false;
                    });
                    $parent.$emit('on-hover-click');
                } else {
                    this.$nextTick(() => {
                        this.d_visible = false;
                    });
                }
            },
            'on-haschild-click' () {
                this.$nextTick(() => {
                    this.d_visible = true;
                });
                const $parent = this.hasParent();
                if ($parent) $parent.$emit('on-haschild-click');
            }
        }
    };
</script>


