<template>
    <div :class="classes">
        <slot></slot>
    </div>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    import { oneOf } from '../../utils/assist';
    import emitter from '../../utils/emitter';
    const prefixCls = `${prefix}radio-group`;

    export default {
        name: 'radioGroup',
        mixins: [emitter],
        props: {
            value: {
                type: [String, Number],
                default: ''
            },
            size: {
                validator (value) {
                    return oneOf(value, ['small', 'large']);
                }
            },
            type: {
                validator (value) {
                    return oneOf(value, ['button']);
                }
            },
            vertical: {
                type: Boolean,
                default: false
            }
        },
        data(){
            return{
                d_model:this.value
            }
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-${this.size}`]: !!this.size,
                        [`${prefixCls}-${this.type}`]: !!this.type,
                        [`${prefixCls}-vertical`]: this.vertical
                    }
                ];
            }
        },
        mounted () {
            this.updateModel();
        },
        methods: {
            updateModel () {
                const model = this.d_model;
                this.$children.forEach((child) => {
                    child.selected = model == child.value;
                    child.group = true;
                });
            },
            change (data) {
                this.d_model = data.value;
                this.updateModel();
                this.$emit('on-change', data.value);
                /**
                 * 功能: oninput事件用于v-model响应
                 * 修改记录: xx
                 * @author 叶灵晓
                 * @date 2017-02-06 14:10
                 */
                this.$emit('input', data.value);
                this.dispatch('form-item', 'on-form-change', [data.value]);
                // this.$emit('on-form-change', data.value);
            }
        },
        watch: {
            //model () {
                //this.updateModel();
           // }
        }
    };
</script>


