<template>
    <ul :class="prefix+'dropdown-menu'"><slot></slot></ul>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    export default {
        data() {
            return {
                prefix: prefix
            }
        }
    };
</script>


