<template>
    <li :class="classes" @click.stop="select" @mouseout.stop="blur" v-show="!hidden"><slot>{{ showLabel }}</slot></li>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    const prefixCls = `${prefix}select-item`;

    export default {
        props: {
            value: {
                type: [String, Number],
                required: true
            },
            label: {
                type: [String, Number]
            },
            disabled: {
                type: Boolean,
                default: false
            }
        },
        componentName: 'select-item',
        data () {
            return {
                selected: false,
                index: 0,    // for up and down to focus
                isFocus: false,
                hidden: false,    // for search
                searchLabel: ''    // the value is slot,only for search
            };
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-disabled`]: this.disabled,
                        [`${prefixCls}-selected`]: this.selected,
                        [`${prefixCls}-focus`]: this.isFocus
                    }
                ];
            },
            showLabel () {
                return (this.label) ? this.label : this.value;
            }
        },
        methods: {
            select () {
                if (this.disabled) {
                    return false;
                }
                //this.$dispatch('on-select-selected', this.value);

                this.$parent.$parent.onSelectSelected(this.value,this.label);
                //this.$emit('on-select-selected',this.value);
            },
            blur () {
                this.isFocus = false;
            },
            queryChange (val) {
                const parsedQuery = val.replace(/(\^|\(|\)|\[|\]|\$|\*|\+|\.|\?|\\|\{|\}|\|)/g, '\\$1');
                this.hidden = !new RegExp(parsedQuery, 'i').test(this.searchLabel);
                // try{
                //     this.hidden = !new RegExp(val, 'i').test(this.searchLabel);
                // }catch(e){
                //     console.warn('搜索字符非法');
                //     this.hidden=true;
                // }

            }
        },
        mounted () {
            /**
             * 功能: xx
             * 修改记录: 挂载后监听select中的on-query-change,根据搜索值匹配判定是否hide
             * option=>dropdown=>select
             * @author 叶灵晓
             * @date 2017-01-19 14:33
             */

            var that=this;
            this.$parent.$on('on-select-close', function(msg) {
				this.isFocus = false;
			})
			this.$parent.$parent.$on('on-query-change', function(val) {
                that.hidden=false;
                that.queryChange(val);
			})
            this.$parent.$parent.$on('on-slot-change',function(){
                this.$nextTick(function () {
                    that.hidden=false;
                   // console.log(that.$el.innerHTML)
                    that.searchLabel = that.$el.innerHTML;
                });
            });

            this.$nextTick(function () {
                //console.log(this.$el.innerHTML)
                this.searchLabel = this.$el.innerHTML;
            });
        },
        events: {
           // 'on-select-close' () {
             //   this.isFocus = false;
            //},
           // 'on-query-change' (val) {
              //  this.queryChange(val);
           // }
        }
    };
</script>


