<template>
    <div :class="classes">
        <slot></slot>
    </div>
</template>
<script>
    import { prefix } from '../../conf';
    import emitter from '../../utils/emitter';
    const prefixCls = `${prefix}checkbox-group`;

    export default {
        name: 'checkboxGroup',
        mixins: [emitter],
        props: {
            value: {
                type: Array,
                default () {
                    return [];
                }
            }
        },
        data(){
            return {
                d_model:this.value
            }
        },
        computed: {
            classes () {
                return `${prefixCls}`;
            }
        },
        mounted () {
            this.updateModel(true);
        },
        methods: {
            updateModel (update) {
                const model = this.d_model;
                this.$children.forEach((child) => {
                    child.model = model;
                    if (update) {
                        child.selected = model.indexOf(child.value) >= 0;
                        child.group = true;
                    }
                });
            },
            change (data) {
                this.d_model = data;
                this.$emit('on-change', data);
                /**
                 * 功能: oninput事件用于v-model响应
                 * 修改记录: xx
                 * @author 叶灵晓
                 * @date 2017-02-06 14:59
                 */
                this.$emit('input', data);
                this.dispatch('form-item', 'on-form-change', [data]);
                //this.$emit('on-form-change', data);
            }
        },
        watch: {
            value(val){
                this.d_model=val;
            },
            d_model () {
                this.updateModel(true)
            }
        }
    };
</script>



