<template>
    <form :class="classes"><slot></slot></form>
</template>

<script type="text/javascript">
    import { prefix } from '../../conf';
    // https://github.com/ElemeFE/element/blob/dev/packages/form/src/form.vue
    import { oneOf } from '../../utils/assist';

    const prefixCls = `${prefix}form`;

    export default {
        name: 'iForm',
        props: {
            model: {
                type: Object
            },
            rules: {
                type: Object
            },
            labelWidth: {
                type: Number
            },
            labelPosition: {
                validator (value) {
                    return oneOf(value, ['left', 'right', 'top']);
                },
                default: 'right'
            },
            inline: {
                type: Boolean,
                default: false
            }
        },
        data () {
            return {
                fields: []
            };
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`,
                    `${prefixCls}-label-${this.labelPosition}`,
                    {
                        [`${prefixCls}-inline`]: this.inline
                    }
                ];
            }
        },
        methods: {
            resetFields() {
                this.fields.forEach(field => {
                    field.resetField();
                });
            },
            validate(callback) {
                let valid = true;
                let count = 0;
                this.fields.forEach(field => {
                    field.validate('', errors => {
                        // console.log("form field error=>")
                        // console.log(errors);
                        if (errors) {
                            valid = false;
                        }
                        if (typeof callback === 'function' && ++count === this.fields.length) {
                            callback(valid);
                        }
                    });
                });
            },
            validateField(prop, cb) {
                const field = this.fields.filter(field => field.prop === prop)[0];
                if (!field) { throw new Error('[iView warn]: must call validateField with valid prop string!'); }

                field.validate('', cb);
            },
            /**
             * 功能: xx
             * 修改记录: addItem removeItem
             * @author 叶灵晓
             * @date 2017-01-22 11:13
             */
            addItem(field){
                if (field) {
                    if (field) this.fields.push(field);
                }
            },
            removeItem(field){
                if (field.prop) this.fields.splice(this.fields.indexOf(field), 1);
            }
        },
        watch: {
            rules() {
                this.validate();
            }
        },
        //
        // events: {
        //     'on-form-item-add' (field) {
        //         if (field) this.fields.push(field);
        //         return false;
        //     },
        //     'on-form-item-remove' (field) {
        //         if (field.prop) this.fields.splice(this.fields.indexOf(field), 1);
        //         return false;
        //     }
        // }
    };
</script>


