<template>
    <a :href="d_to"  :class="classes" :title="title">
        <Icon :type="icon" v-if="icon"></Icon>
        <span v-if="showSlot" ref="slot"><slot></slot></span>
    </a>

</template>
<script type="text/javascript">
    import { prefix } from '../../conf';
    import Icon from '../icon';
 import {oneOf} from '../../utils/assist';
 const prefixCls = `${prefix}btn`;
 export default {
     components:{Icon},
     props: {
         to: {
              type: String,
              required: true
         },
        icon: String,
        shape: {
             validator (value) {
               return oneOf(value, ['circle', 'circle-outline']);
             }
         },
         title: String,
         size: {
             validator (value) {
                 return oneOf(value, ['small', 'large']);
             }
         }
    },
    data() {
        return {
             d_to: this.to,
            showSlot:true
        }
    },
     computed:{
         classes(){
             return [
                 `${prefixCls}`,
                 {
                     [`${prefixCls}-${this.icon}`]: !!this.icon,
                     [`${prefixCls}-${this.shape}`]: !!this.shape,
                     [`${prefixCls}-${this.size}`]: !!this.size,
                     [`${prefixCls}-icon-only`]: !this.showSlot && (!!this.icon)
                 }
             ];
         }

     },
    watch:{
        to(val){
            this.d_to=val;
        }
    },
     mounted(){
         this.showSlot = this.$refs.slot.innerHTML.replace(/\n/g, '').replace(/<!--[\w\W\r\n]*?-->/gmi, '') !== '';
     }


 }
</script>



