<template>
    <div :class="classes" >
        <template v-if="renderType === 'index'">{{ pageSize*(pageNo-1)+ naturalIndex+ 1}}</template>
        <template v-if="renderType === 'selection'">
            <Checkbox :checked="checked" @on-change="toggleSelect"></Checkbox>
        </template>
        <template v-if="renderType === 'normal'"><div v-html="row[column.key]"></div></template>
        <template v-if="renderType === 'render'">
            <span v-for="o in opration"  ref="opration">
                <a-button :type="o.type||'primary'" :icon="o.icon" :shape="o.shape||'circle'" @click="o.click" :title="o.title||''"  size="small"   v-if="o.tagName==='button'">{{o.text}}</a-button>
                <a-link :to="o.to" size="small" :icon="o.icon" :shape="o.shape||'circle'" :title="o.title||''"  v-if="o.tagName==='a'">{{o.text}}</a-link>
                <a-icon :size="o.size" :type="o.type" :title="o.title||''" :color="o.color" v-if="o.tagName==='icon'"></a-icon>
            </span>
            <span v-if="isNomalRender">{{opration}}</span>
        </template>
    </div>
</template>

<script type="text/javascript">
    import Checkbox from '../checkbox/checkbox.vue';
    import aButton from '../button/button.vue';
    import aLink from '../link/a-link.vue';
    import aIcon from '../icon/icon.vue';
    import emitter from '../../utils/emitter';
    export default {
        componentName:'cell',
        mixins: [emitter],
        components: { Checkbox,aButton,aLink,aIcon},
        props: {
            prefixCls: String,
            row: Object,
            column: Object,
            naturalIndex: Number,    // index of rebuildData
            index: Number,           // _index of data
            checked: Boolean,
            fixed: {
                type: [Boolean, String],
                default: false
            },

        },
        data () {
            return {
                opration:[],
                template:'',
                renderType: '',
                uid: -1,
                content: this.$parent.$parent.d_content,
                isNomalRender:true,
                pageSize:0,
                pageNo:1
               // d_checked:this.checked
            };
        },
        computed: {
            classes () {
                return [
                    `${this.prefixCls}-cell`,
                    {
                        [`${this.prefixCls}-hidden`]: !this.fixed && this.column.fixed && (this.column.fixed === 'left' || this.column.fixed === 'right'),
                        [`${this.prefixCls}-cell-ellipsis`]: this.column.ellipsis || false
                    }
                ];
            },

        },
        methods: {
            compile () {
                if (this.column.render) {
                    const template = this.column.render(this.row, this.column, this.index);
                    if(template instanceof Array){
                       this.isNomalRender=false;
                    }
                    this.opration=template;

                   const $parent = this.content;
                   //  const template = this.column.render(this.row, this.column, this.index);
                   //  let vtpl= Vue.compile(template);
                   //  const cell = document.createElement('div');
                   //  cell.innerHTML = template;
                    const _oldParentChildLen = $parent.$children.length;
                   // // $parent.$compile(cell);
                    const _newParentChildLen = $parent.$children.length;
                   //
                    if (_oldParentChildLen !== _newParentChildLen) {    // if render normal html node, do not tag
                        this.uid = $parent.$children[$parent.$children.length - 1]._uid;    // tag it, and delete when data or columns update
                    }
                   //  this.$el.innerHTML = '';
                   //  this.$el.appendChild(cell);
                }
            },
            destroy () {
                const $parent = this.content;
                for (let i = 0; i < $parent.$children.length; i++) {
                    if ($parent.$children[i]._uid === this.uid) {
                       // console.log($parent.$children[i]);
                        $parent.$children[i].$destroy();

                    }
                }
            },
            toggleSelect (check) {
              // this.d_checked=check;
                this.$parent.$parent.toggleSelect(this.index,check);
            }
        },
        mounted () {
            if (this.column.type === 'index') {
                this.renderType = 'index';
            } else if (this.column.type === 'selection') {
                this.renderType = 'selection';
            } else if (this.column.render) {
                this.renderType = 'render';
            } else {
                this.renderType = 'normal';
            }
                this.compile();
            this.pageNo=this.$parent.$parent.pageNo;
            this.pageSize=this.$parent.$parent.pageSize;
            this.$on('pageNoChange',function(val){
                this.pageNo=val;
            });
            this.$on('pageSizeChange',function(val){
                this.pageSize=val;
            });
        },
        created(){
           // console.log(this.column.render)
            // console.log(this.$options.template)
        },
        beforeDestroy () {
            this.destroy();
        },
        watch: {
            naturalIndex () {
                this.destroy();
                this.compile();
            },
            checked(val){
              //this.d_checked=val;
            },
            row(val){
                this.compile();
            }
        }
    };
</script>


