<template>
    <li :class="classes" @click="click">{{ data.label }}<i v-if="data.children && data.children.length" :class="[prefix+'icon', prefix+'icon-ios-arrow-right']"></i></li>
</template>
<script>
    import { prefix } from '../../conf';
    export default {
        props: {
            data: Object,
            prefixCls: String,
            tmpItem: Object
        },
        data () {
            return {
                prefix: prefix
            }
        },
        methods:{
            click(e){
                this.$emit('click',e);
            }
        },
        computed: {
            classes () {
                return [
                    `${this.prefixCls}-menu-item`,
                    {
                        [`${this.prefixCls}-menu-item-active`]: this.tmpItem.value === this.data.value,
                        [`${this.prefixCls}-menu-item-disabled`]: this.data.disabled
                    }
                ];
            }
        }
    };
</script>



